import Vue from 'vue';
import Router from 'vue-router';
import Index from './pages/Home';
import Publications from './pages/Publications';
import Team from './pages/Team';
import News from './pages/News'
import Projects from './pages/Projects';
import SmartCity from './pages/SmartCity/Index';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: "/",
      name: "Index",
      component: Index
    },
    {
      path: "/publications",
      name: "Publications",
      component: Publications
    },
    {
      path: "/team",
      name: "Team",
      component: Team
    },
    {
      path: "/projects",
      name: "Projects",
      component: Projects
    },
    {
      path: "/smartCity",
      name: "SmartCity",
      component: SmartCity
    },
    {
      path: "/news",
      name: "News",
      component: News
    }
  ]
});
