<template>
  <v-row justify="center">
    <p class="cor-botao" dark @click.stop="dialog = true" >
      {{ info }}
    </p>
    <v-dialog v-model="dialog" >
      <v-card>
        <v-container>
            <v-row class="main_card">
                <v-col sm="4" class="main_bio">
                  <v-img class="main_image" v-bind:src="person.pic">
                  </v-img>
                  <v-card-text class="main_info">
                    <h1 class="texto name_title"> > {{ person.name }} </h1>
                    <h1 class="texto name_email"> > {{ person.email }} </h1>
                    <p class="texto name_bio"> {{ person.bio }} </p>
                    <v-btn class="button" width="100%" color="blue">  Linked In </v-btn>
                  </v-card-text>
                </v-col>
                <v-col sm="8" class="main_body">
                  <h1> Publications </h1>
                  <publi v-bind:name="person.name"></publi>
                </v-col>
            </v-row>
            <Cards></Cards> 
        </v-container>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
import publi from '../Team/PersonalPubli.vue'
import MockData from '../../common/texts/MockData';

  export default {
    props: [ 'info' ],
    components: { publi },
    data () {
      return {
        dialog: false,
        allPerson: MockData.team,
        person: ''
      }
    },
    mounted(){
      this.acontece();
    },
    methods: {

      acontece() {
        for(let i = 0; i < this.allPerson.length; i++){
          if(this.allPerson[i].name == this.info){
            this.person = this.allPerson[i]
          }
        }
      }
    }
  }
</script>

