<template>
  <v-expansion-panels popout focusable>
    <v-expansion-panel v-for="item in auxiliar" :key="item.year">
      <v-expansion-panel-header class="main_text"> <b> {{item.year}} </b>
      </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-divider></v-divider><br>
            <div v-for="unit in item.publi" :key="unit">
                <p class="publi publi_title">{{ unit.title }}</p>
                <div v-for="pessoa in unit.names" :key="pessoa">
                        <div v-if="pessoa == name">
                            <p class="publi publi_names_focus"><u><b>{{ pessoa }}</b></u></p>
                        </div>
                        <div v-else>
                            <p class="publi publi_names">{{ pessoa }}</p>
                        </div>
                </div> 
                <p class="publi publi_link">{{ unit.link }}</p> 
                <br>
            </div>
        </v-expansion-panel-content>
    </v-expansion-panel>
  </v-expansion-panels>
</template>

<script>
import MockData from '../../common/texts/MockData'

export default {
    props: [ 'name' ],
    data(){
        return {
            items: MockData.publications,
            auxiliar: [],
        }
    },
    mounted(){
        this.filter();
    },
    methods: {
        filter(){
            let umAno = 0;
            let remove = 0;
            let entrou = 0;
            for(let i = 0; i < this.items.length; i++){ // Pega a qtd de jsons e itera
                for(let j = 0; j < this.items[i].publi.length; j ++){ //Pega a qtd de publi e itera
                    for(let k = 0; k < this.items[i].publi[j].names.length; k++){ // pega a qtd de nome e itera
                        if(this.items[i].publi[j].names[k] == this.name){ // compara o nome com o recebido
                            if(umAno == 0){ //Se pessoa publicou mais de duas vezes em um ano, só inclui um ano
                                this.auxiliar.push(this.items[i]) //inclui a publicação em auxiliar
                                umAno = 1; //Bloqueia incluir o ano mais de uma vez
                                entrou = 1;
                            }
                        }
                        
                    }
                    entrou = 0
                }
                umAno = 0;
            }
        }
    }
}
</script>

